@import "../../../variables.scss";

.swiperSlide {
    img {
        width: 100%;
        height: 100% !important;
    }
}
.swiperContainer {
    @media (min-width: 992px) {
        padding-bottom: 1.875em;
    }
    div:nth-of-type(1) {
        @media (min-width: 992px) {
            height: 0.625em;
            background-color: $headerText;
            border-radius: 0;
        }
        div:nth-child(1) {
            @media (min-width: 992px) {
                background-color: $headerTextSecondary;
            }
        }
    }
}
